.Article {

}

.Article-container {
  display: grid;
  grid-template-columns: 80% auto;
  grid-template-rows: auto;
  grid-template-areas: 
    "left right";
  gap: 5px 5px;
}

.Article-left {
  grid-area: left;
}

.Article-right {
  grid-area: right;
}

.Article-h1 {
  color: rgb(47, 116, 172)
}

.Article-img {
  width: 100%;
}

.Article-note {
  background-color: rgb(237, 200, 126);
  width: 40%;
  border-radius: 5px;
  padding: 5px 5px 5px 5px;
}

.Article-annotation {
  background-color: rgb(248, 247, 173);
  border-radius: 5px;
  padding: 5px 5px 5px 5px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.Article-center-image {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.Article-figcaption {
  display: block;
  margin-left: auto;
  margin-right: auto;
  font-size: small;
  text-align: center;
}