.MainPage {
  display: grid;
  grid-template-columns: 15% auto;
  grid-template-rows: auto;
  grid-template-areas: 
    "header header"
    "sidebar-left main"
    "footer footer";
  gap: 5px 5px;
}

.MainPage-header {
  grid-area: header;
}

.MainPage-main {
  grid-area: main;
  
  background-color: #FEF7EA;
  padding: 5px;
}

.MainPage-sidebar-left {
  grid-area: sidebar-left;
  overflow-wrap: break-word;
  color: #ccc;
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto;
  row-gap: 5px;
  padding-left: 5px;
}

.MainPage-sidebar-left a {
  color:#ccc;
}

.MainPage-footer{
  grid-area: footer;
}

.MainPage-copyright {
  color: #ccc;
}

.MainPage-logo {
  width: 200px;
}