.QuizCards {
  
}

.answerButton {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  transition: all 0.3s ease;

}

.answerButton:hover {
  opacity: 0.8;
}

button {
  cursor: pointer;
  user-select: none;
  width: 50%;
  margin-bottom: 5px;
  border-radius: 5px;
  padding: 5px 10px;
  font-size: 0.8rem;
  border: 1px solid #aaa;
}

.buttonRightAnswer {
  background-color: rgb(66, 255, 66);
}

.buttonWrongAnswer {
  background-color: rgb(241, 73, 73);
}